@use '../../scss/colors.scss';
@use '../../scss/typography.scss';
@use '../../scss/variables.scss';

.clock {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  width: variables.$clock-width;
  height: 100%;
  padding: 0 1.5em;
  background-image: linear-gradient(180deg, colors.$clock-lighter 0%, colors.$clock-darker 100%);
  transform-origin: center 0%;

  &Wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }
}

.time {
  position: absolute;
  top: 50%;
  right: 0;
  display: flex;
  align-items: center;
  margin-left: 0.5em;
  color: colors.$white-text;
  font-size: 2em;
  font-family: typography.$clock-font-family;
  // transform: translate(-50%, -50%);
  transform: translateY(-50%);
  user-select: none;
}

.separator {
  transform: translateY(-5%);
}
